import FavoritePlaceIdb from '../data/favorite-place-idb'
import {
  createLikePlaceButtonTemplate,
  createUnlikePlaceButtonTemplate
} from '../views/templates/template-creator'

const LikeButtonPresenter = {
  async init({ likeButtonContainer, place }) {
    this._likeButtonContainer = likeButtonContainer
    this._place = place
    await this._renderButton()
  },
  async _renderButton() {
    const { id } = this._place
    if (await this._isPlaceExist(id)) {
      this._renderLiked()
    } else {
      this._renderLike()
    }
  },
  async _isPlaceExist(id) {
    const place = await FavoritePlaceIdb.getPlace(id)
    return !!place
  },

  _renderLike() {
    this._likeButtonContainer.innerHTML = createLikePlaceButtonTemplate()

    const likeButton = document.querySelector('#likeButton')
    likeButton.addEventListener('click', async () => {
      await FavoritePlaceIdb.putPlace(this._place)
      this._renderButton()
    })
  },

  _renderLiked() {
    this._likeButtonContainer.innerHTML = createUnlikePlaceButtonTemplate()

    const likeButton = document.querySelector('#likeButton')
    likeButton.addEventListener('click', async () => {
      await FavoritePlaceIdb.deletePlace(this._place.id)
      this._renderButton()
    })
  }
}

export default LikeButtonPresenter
